.container {
    max-width: 800px;
    padding: 0 10px;
    margin: 20px auto;

}

h2 {
    text-align: center;
    margin-bottom: 20px;
}

.board-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.board-table th {
    border:  1px solid #dddddd;
    padding: 10px;
    text-align: center;
    background-color: #f4f4f4;
}

.board-table td {
    border:  1px solid #dddddd;
    padding: 10px;
    text-align: center;
}

.write-button-container {
    text-align: right;
}

.write-button {
    background-color: #1e88e5;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-bottom: 20px;
}

.write-button:hover {
    background-color: #1871be;
    color: white;
}

.click-title {
    cursor: pointer;
    color: #1e88e5;
}

.click-title:hover {
    text-decoration: underline;
    font-weight: bold;
}

.pagnation {
    margin-top: 20;
    display: flex;
    justify-content: center;
    gap: 5px;
}

.pagnation button {
    padding: 5px 10px;
    border:  1px solid #dddddd;
    border-radius: 3px;
    cursor: pointer;
    background-color: white;
}

.pagnation button.active {
    background-color: rgb(155, 155, 155);
    color: white;
    font: bold;
}

.pagnation button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}