.detail-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.detail-container h2 {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
}

.detail-container .author {
    font-size: 14px;
    color: rgb(56, 56, 56);
    margin-bottom: 20px;
}

.detail-container .content {
    min-height: 150px;
    border: 1px solid #dddddd;
    border-radius: 5px;
    background-color: rgb(252, 252, 252);
    margin-bottom: 20px;
    padding: 10px;
    font-size: 16px;
    white-space: pre-wrap; /* 줄 바꿈 유지 */
    line-height: 1.5;
}

.list-button:hover {
    background-color: #1871be;
}

.delete-button:hover{
    background-color: #582828;
}

.edit-button:hover {
    background-color: #1871be;
}

/* 글 수정 */
.edit-form input {
    width: 100%;
    border: 1px solid #dddddd;
    margin-bottom: 10px;
    font-size: 16px;
    border-radius: 5px;
    padding: 10px;
    height: 40px;
    box-sizing: border-box;
}

.edit-form textarea {
    width: 100%;
    border: 1px solid #dddddd;
    margin-bottom: 10px;
    font-size: 16px;
    border-radius: 5px;
    padding: 10px;
    min-height: 120px;
    resize: vertical;
    box-sizing: border-box;
}

/* 댓글 영역 */
.comment-section {
    padding:  20px;
    margin-top: 40px;
    background-color: rgb(250, 250, 250);
    border-radius: 12px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.comment-section h3 {
    font-size: 20px;
    margin-bottom: 15px;
    color: #4d4a4a;
}

/* 댓글 쓰기 */
.comment-form {
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.comment-form textarea {
    min-height: 80px;
    padding: 12px;
    border: 1px solid #cccccc;
    border-radius: 10px;
    font-size: 14px;
    resize: none;
}

.comment-form textarea:focus {
    border-color: rgb(0, 0, 112);
    background-color: aliceblue;
    outline: none;
}

.comment-save,
.comment-cancel,
.comment-edit,
.comment-delete,
.comment-button {
    background-color: #1e88e5;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-bottom: 20px;
    width: 50px;
    align-self: flex-end;
}

.comment-save:hover,
.comment-cancel:hover,
.comment-edit:hover,
.comment-delete:hover,
.comment-button:hover {
    background-color: #135792;
}



/* 댓글 리스트 */
.comment-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.comment-list li {
    background-color: white;
    border: 1px solid #eeeeee;
    border-radius: 10px;
    padding: 10px 15px;
    margin-bottom: 12px;
}

.comment-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 7px;
    background-color: rgba(0, 0, 0, 0.03);
    padding: 5px;
    border-radius: 3px;
}

.comment-auther {
    font-weight: bold;
    color: #1e88e5;
}

.comment-date {
    font-size: 12px;
    color: #4d4a4a;
}

.comment-item textarea {
    width: 100%;
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #cccccc;
    resize: none;
    margin-top: 10px;
    font-size: 14px;
    box-sizing: border-box;
}

.comment-savegroup {
    display: flex;
    gap: 10px;
    justify-content: end;
}